.btn-cta{
    display: inline-block;
    padding: 1rem 1.5rem;
    background-image: linear-gradient($colour-secondary, $colour-primary);
    color: getFontColor($colour-primary);
    border-radius: 25rem;
    text-decoration: none;
    box-shadow: $b-shadow;
    opacity: 1;
    transition: all 0.3s ease 0s;

    @include font-style($font-size: 1rem, $font-weight : $f-w-semibold);

    &--large{
        padding: 1rem 4rem;
    }

    &:hover{
        opacity: 0.8;
        box-shadow: $b-shadow-hover;
        //background-image: linear-gradient(lighten($colour-secondary, 5%), lighten($colour-primary, 5%));
    }


}

.whishlist{


    &__heart {
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 25px;




    }


}