.header{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    &__figure{
      flex-basis: 100%;
      background-color: $background-base;
      text-align: center;
      padding: 1.3rem 0 0.8rem 0;
    }
    &__form{
      flex-basis: 100%;
      text-align: center;
      padding: 1.2rem 0 1rem 0;
    }
}

.header-nav{
    background-color: $background-base;

    &__bar{
        position: relative;
        height: 55px;
        text-align: center;

        & .nav-logo{
            img{
                margin-top: 1rem;
                height: 25px;
            }
        }
        & .btn-return{
            position: absolute;
            top: 0;
            left: 0;
            width: 70px;
            height: 60px;
            i{
                position: absolute;
                top: 45%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }

    }

}